/* eslint-disable react/jsx-props-no-spreading */
import React from 'react';
import {
  Card, Form, Input, Button,
} from 'antd';
import ReCAPTCHA from 'react-google-recaptcha';

const layout = {
  labelCol: { span: 8 },
  wrapperCol: { span: 16 },
};
const tailLayout = {
  wrapperCol: { offset: 8, span: 16 },
};

const ContactForm = () => {
  const recaptchaRef = React.createRef();
  const onSubmitWithReCAPTCHA = async () => {
    const token = await recaptchaRef.current.executeAsync();
    return token;
    // apply to form data
  };
  // Case: Submit button out of Form
  const handleFormSubmit = (values) => {
    const recaptchaValue = onSubmitWithReCAPTCHA();

    console.log('recaptchaValue', recaptchaValue);

    const requestOptions = {
      method: 'POST',
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify({ ...values, grecaptcharesponse: recaptchaValue }),
    };
    fetch('https://formspree.io/f/mvodkbbl', requestOptions)
      .then((response) => response.json())
      .then((response) => console.log(response));
    // Submit values
    // submitValues(values);
  };

  return (

    <Card
      title="Info"
      bordered
    >
      <Form
        {...layout}
        name="basic"
        initialValues={{ remember: true }}
        onFinish={handleFormSubmit}
      >
        <Form.Item
          label="name"
          name="name"
          rules={[{ required: true, message: 'Please input your name!' }]}
        >
          <Input />
        </Form.Item>

        <Form.Item
          label="email"
          name="email"
          rules={[{ required: true, message: 'Please input your email!' }]}
        >
          <Input />
        </Form.Item>
        <ReCAPTCHA
          ref={recaptchaRef}
          size="invisible"
          sitekey="6Le53NcaAAAAAIjRwPKKM18-i_xzqgTc2AItuUSA"
        />
        ,
        <Form.Item {...tailLayout}>
          <Button type="primary" htmlType="submit">
            Submit
          </Button>
        </Form.Item>
      </Form>
    </Card>
  );
};

export default ContactForm;
